// request
// class:
let Base64 = require('./base64.js').Base64

//
// import interceptorfun from './interceptor'
// interceptorfun()

const request = class {
	constructor(url, arg) {
		this.url = url
		this.arg = arg
	}

	// post
	modepost() {
		uni.showLoading({
			title: ''
		});
		return new Promise((resolve, reject) => {
			uni.request({
					url: this.url,
					method: 'POST',
					data: this.arg,
					header: {
						Authorization: this.baseFun()
					}
				})
				.then(res => {
					this.valid(res[1].data)
					uni.hideLoading();
					resolve(res[1].data)

				})
				.catch(err => {
					this.validerror()
					uni.hideLoading();
					reject('')
				})
		})
	}

	// get
	modeget() {
		uni.showLoading({
			title: ''
		});
		return new Promise((resolve, reject) => {
			uni.request({
					url: this.url,
					method: 'GET',
					header: {
						Authorization: this.baseFun()
					}
				})
				.then(res => {
					this.valid(res[1].data)
					uni.hideLoading();
					resolve(res[1].data)
				})
				.catch(err => {
					this.validerror()
					uni.hideLoading();
					reject('')
				})
		})
	}

	// delete
	modedelete() {
		uni.showLoading({
			title: ''
		});
		return new Promise((resolve, reject) => {
			uni.request({
					url: this.url,
					method: 'DELETE',
					header: {
						Authorization: this.baseFun()
					}
				})
				.then(res => {
					this.valid(res[1].data)
					uni.hideLoading();
					resolve(res[1].data)

				})
				.catch(err => {
					this.validerror()
					uni.hideLoading();
					reject('')
				})
		})
	}


	baseFun() {
		const token = uni.getStorageSync('accessLink')
		console.log(token)
		return token
	}

	valid(obj) {
		if (obj.code == '2001' || obj.code == '5000') {
			uni.showToast({
				title: obj.userMsg,
				icon: 'none',
				duration: 1500
			})
		}
		// uni.showToast({
		// 	title: obj.userMsg,
		// 	duration: 1500
		// });
	}

	validerror() {
		uni.showToast({
			title: '',
			icon: 'none',
			duration: 1500
		})
	}

}

export default request
